<template>
  <div>
    <v-layout row justify-center>
      <v-form class="pa-5">
        <h2>Nuevos Datos</h2>
        <v-text-field v-model="nombre" label="Nombre" placeholder="Nombre" outlined></v-text-field>
        <v-text-field v-model="apellido" label="Apellido" placeholder="Apellido" outlined></v-text-field>
        <v-btn color="warning" @click="guardar">Guardar</v-btn>
        <!-- <button class="v-btn" color="primary" type="submit" value="acceder">Submit</button> -->
      </v-form>
    </v-layout>
  </div>
</template>

<script>
import firebase from "firebase/app";
import "firebase/firestore";
const db = firebase.firestore();
//const dbf = firebase.database();
export default {
  data() {
    return {
      nombre: "",
      apellido: "",
    };
  },
  methods: {
    enviar() {
      console.log("quee algo deberia estar pasando");
      console.log(firebase.auth().currentUser.uid);
      /* db.on('value', snap => {
               console.log(snap.val())
           }) */
      /* db.collection('nuevosdatos').get().then((snap) =>{
            snap.forEach((doc) =>{
                console.log(doc.id + "_" + doc.data().id_user)
            })
        }) */
    },
    async guardar() {
      
      var docs = await db
        .collection("nuevosdatos")
        .where("id_user", "==", firebase.auth().currentUser.uid)
        .get();
      var id;
      docs.forEach(function (doc) {
        id = doc.id;
        console.log(doc.id, " => ", doc.data());
      });
      console.log(id);
       

      if (id == undefined) {

        db.collection("nuevosdatos")
          .add({
            nombre: this.nombre,
            apellido: this.apellido,
            id_user: firebase.auth().currentUser.uid,
          })
          .then(function (docRef) {
            console.log("Document written with ID: ", docRef.id);
          })
          .catch(function (error) {
            console.error("Error adding document: ", error);
          });

      } else {
          console.log("esto es de el else")
        db.collection("nuevosdatos")
          .doc(id)
          .update({
            nombre: this.nombre,
            apellido: this.apellido,
          })
          .then((res) => console.log("salió bien"))
          .catch((error) => console.log("algo salio mal: " + error));
      }

    },
    
  },
};
</script>

